.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;

    @media screen and (min-width: 480px) {
        max-width: 480px;
    }

    @media screen and (min-width: 768px) {
        max-width: 768px;
        padding-left: 32px;
    padding-right: 32px;
    }

    @media screen and (min-width: 1200px) {
        max-width: 1200px;
    }
}

body {
    font-family: 'Montserrat';
}

img {
    display: block;
    width: 100%;
    max-width: 100%;
    height: auto;
}